/* =============================================
9.0 Breadcrumb
================================================ */
.breadcrumb {
  padding: $breadcrumb-padding-y $breadcrumb-padding-x;
  font-family: $breadcrumb-font-family;
  font-weight: $breadcrumb-font-weight;
  text-transform: uppercase;
  background-color: $breadcrumb-bg;

  a {
    transition: all $transition-duration-base ease-in-out;
  }
}

.breadcrumb-light {

  li,
  .active {
    color: $breadcrumb-light-link-color;
  }

  a {
    color: $breadcrumb-light-link-color;

    &:hover {
      color: $breadcrumb-light-link-color-hover;
    }
  }

  .breadcrumb-item {
    + .breadcrumb-item {
      &::before {
        color: $breadcrumb-light-separate-color;
      }
    }
  }
}
