/* =============================================
2.0 Typography
================================================ */
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: $font-family-secondary;
  font-weight: $headings-font-weight;
  line-height: $headings-line-height;
  color: $heading-color-base;
}

.lead {
  font-family: $font-family-secondary;
  font-size: $lead-font-size;
  font-weight: $lead-font-weight;
}

.section-title {
  font-size: $headings-font-size;
  font-weight: $headings-font-weight;
}

.blockquote {
  position: relative;
  padding: $blockquote-pt $blockquote-px 0;
  font-family: $blockquote-font-family;
  font-size: $blockquote-font-size;

  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: $blockquote-icon-width;
    height: $blockquote-icon-height;
    background-image: $blockquote-icon-bg-img;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}

.blockquote-footer {
  margin-top: $blockquote-footer-mt;
  font-size: $blockquote-small-font-size;
  color: $blockquote-small-color;

  &::before {
    display: none;
  }
}

.drop-cap {
  float: left;
  width: $drop-cap-width;
  height: $drop-cap-width;
  margin: $drop-cap-mt $drop-cap-mr 0 0;
  font-family: $drop-cap-font-family;
  font-size: $drop-cap-font-size;
  line-height: $drop-cap-line-height;
  text-align: center;
  color: $drop-cap-color;
  background-color: $drop-cap-bg-color;
  border-radius: 50%;
}
