/* =============================================
13.0 FlexSlider
================================================ */
.flexslider {
  overflow: hidden;
  margin-bottom: 0;
  border: none;
  border-radius: 0;

  &:hover {
    .flex-direction-nav {

      .flex-next {
        right: 0;
      }

      .flex-prev {
        left: 0;
      }
    }
  }
}

.flex-direction-nav {
  a {
    margin-top: $flex-direction-nav-mt;
    height: $flex-direction-nav-height;
    opacity: $flex-direction-nav-opacity !important;

    &:hover {
      opacity: 1 !important;
    }

    &::before {
      content: '' !important;
      height: $flex-direction-nav-height;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }

    &.flex-next {
      right: 0;
      background-image: $flex-direction-next-bg-image;
    }

    &.flex-prev {
      left: 0;
      background-image: $flex-direction-prev-bg-image;
    }
  }
}

/* ========== Media query breakpoint ========== */
@include media-breakpoint-up(xl) {

  .flexslider {

    .flex-direction-nav {

      .flex-next {
        right: $flex-direction-nav-offset;
      }

      .flex-prev {
        left: $flex-direction-nav-offset;
      }
    }

    &:hover {
      .flex-direction-nav {

        .flex-next {
          right: 0;
        }

        .flex-prev {
          left: 0;
        }
      }
    }
  }

}
