/* =============================================
5.0 Layouts
================================================ */
.page-bg-image {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.page-title {
  min-height: $page-title-min-height;
}

.content-box {
  padding-top: $content-box-pt-sm;
  padding-bottom: $content-box-pb;
}

/* ========== Media query breakpoint ========== */
@include media-breakpoint-up(xl) {

  .content-box {
    padding-top: $content-box-pt-xl;
  }

}
