/* =============================================
15.0 Portfolio
================================================ */

/* =============================================
15.1 Portfolio grid items
================================================ */
.portfolio-filter-wrapper {
  padding: $portfolio-filter-py 0;

  ul {
    padding: 0;
    margin-bottom: 0;
  }

  .filter {
    display: inline-block;
    padding-top: 0 !important;
    padding-bottom: 0 !important;
    margin: $portfolio-filter-list-my 0;
  }
}

.portfolio-grid-wrapper {
  position: relative;
  overflow: hidden;
}

/* =============================================
15.2 Portfolio single
================================================ */
.portfolio-img-wrapper {
  margin-bottom: $portfolio-featured-img-mb;
}

.portfolio-single-title {
  margin-bottom: $portfolio-single-title-mb;
  font-size: $portfolio-single-title-font-size;
  text-transform: uppercase;
}

.portfolio-metadata {
  padding-left: $portfolio-metadata-pl;
  margin-bottom: $portfolio-metadata-mb;
  border-left: $portfolio-metadata-border-width solid $portfolio-metadata-border-color;

  ul {
    margin-bottom: $portfolio-metadata-ul-mb;

    li {
      margin-bottom: $portfolio-metadata-li-mb;
      font-family: $portfolio-metadata-font-family;
      font-weight: $portfolio-metadata-font-weight;
      color: $portfolio-metadata-color;
      text-transform: uppercase;

      span {
        color: $portfolio-metadata-value-color;
      }
    }
  }
}

.related-work-title {
  margin-bottom: $related-work-heading-mb;
  font-size: $related-work-font-size;
  text-transform: uppercase;
}

.related-work-thumbnail {
  margin-bottom: $related-work-mb;

  .hover-item {
    > img {
      width: 100%;
    }
  }

  .hover-overlay {
    &::before,
    &::after {
      display: none;
    }
  }

  .related-work-info {
    padding: 0 $related-work-info-px;
    width: 100%;
    text-align: center;
    transition: all $transition-duration-base ease-in-out;
    transform: translateY(15px);

    img {
      margin-bottom: $related-work-info-icon-mb;
    }

    h6 {
      margin-bottom: 0;
      color: $related-work-info-color;
      text-transform: uppercase;
    }
  }

  &:hover {
    .related-work-info {
      transform: translateY(0);
    }
  }
}

/* ========== Media query breakpoint ========== */
@include media-breakpoint-up(lg) {

  .portfolio-img-wrapper {
    margin-bottom: 0;
  }

}
