20.0 Map
================================================ */
.map-container {
  height: $map-height-sm;

  iframe {
    width: 100%;
    height: 100%;
  }
}

/* ========== Media query breakpoint ========== */
@include media-breakpoint-up(md) {

  .map-container {
    height: $map-height-md;
  }

}
