/* =============================================
16.0 Team
================================================ */
.team-section {
  background-image: $team-section-bg-img;
  background-repeat: repeat;
}

.team-section-title {
  position: relative;
  margin-right: auto;
  margin-left: auto;
  font-size: $team-section-title-font-size;
  text-shadow: $team-section-title-shadow;

  &::after {
    position: absolute;
    content: '';
    width: 100%;
    height: 1px;
    top: 50%;
    left: 0;
    background-color: $divider-dark-color;
    transform: translateY(-50%);
  }

  span {
    position: relative;
    z-index: 1;
    background-image: $team-section-bg-img;
    background-repeat: repeat;
  }
}

.team-item {
  position: relative;

  .hover-overlay {
    &::before,
    &::after {
      display: none;
    }
  }
}

.team-mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.team-info-wrapper {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  .team-info {
    font-family: $team-info-font-family;
    opacity: 0;
    visibility: hidden;
    transition: all ( $transition-duration-base + .1 ) ease-in-out;
    transform: translateY(30px);
  }

  &:hover {
    .team-info {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
    }
  }
}

.team-name {
  font-size: $team-name-font-size;
  font-weight: $team-name-font-weight;
}

.team-position {
  font-size: $team-position-font-size;
  font-weight: $team-position-font-weight;
}

.team-link {
  font-size: $team-link-font-size;
  font-weight: $team-link-font-weight;
  transition: all $transition-duration-base ease-in-out;
}

/* ========== Media query breakpoint ========== */
@include media-breakpoint-up(md) {

  .team-section-title {
    width: $team-section-title-width-md;
  }

}
