/* =============================================
7.0 Navigation
================================================ */
.navik-header {
  border-bottom: $menu-border-color solid $menu-border-width-sm;
  z-index: $menu-container-z-index;

  .logo {
    padding: 0;
    text-align: left;

    img {
      width: auto;
      max-height: $logo-max-height-sm;
      margin-left: $logo-mx-sm;
    }
  }
}

.burger-menu {
  top: $burger-menu-top;
  right: $burger-menu-right;

  &.menu-open {
    transform: rotate(0);
  }
}

.line-menu {
  height: $burger-menu-height;
  background-color: $burger-menu-color;
  border-radius: 0;
}

.navik-menu {
  > ul {
    padding-bottom: 0;
  }

  ul {
    padding: 0;
    list-style: none;

    ul {

      li {
        
        > a {
          padding-right: $menu-list-pr-sm;
          padding-left: $secondary-menu-list-pl-sm;
          font-size: $submenu-font-size;
          font-style: normal;
          background-color: transparent;
        }

        &:hover {
          > a {
            background-color: $menu-active-bg;
          }
        }
      }

      ul {
        li {
          > a {
            padding-right: $menu-list-pr-sm;
            padding-left: $third-menu-list-pl-sm;
          }
        }
      }
    }

    li {

      > a {
        padding-right: $menu-list-pr-sm;
        padding-left: $menu-list-pl-sm;
        font-family: $font-family-secondary;
        color: $menu-color-base;
      }

      .dropdown-plus {
        &::before,
        &::after {
          top: $plus-menu-top;
          right: $plus-menu-right;
          width: $plus-menu-width;
          height: $plus-menu-height;
          background-color: $menu-caret-color;
          transition: all $transition-duration-base ease-in-out;
        }
      }

      &.current-menu,
      &:hover {

        > a {
          color: $menu-active-color;
          background-color: $menu-active-bg;
        }

        > .dropdown-plus {
          &::before,
          &::after {
            background-color: $menu-active-color;
          }
        }
      }
    }
  }
}

/* ========== Media query breakpoint ========== */
@include media-breakpoint-up(xl) {

  .navik-header {
    border-width: $menu-border-width-xl;

    .logo {
      top: 0;
      transform: translateY(0);

      img {
        max-height: inherit;
        margin-left: 0;
      }
    }

    &.header-opacity-on {
      background-color: $menu-header-bg-color-xl;
    }

    &.sticky {

      .navik-header-container {
        height: auto;
        min-height: $menu-container-height-xl;
      }

      .logo {
        padding: 0;
      }

      .navik-menu {
        > ul {
          > li {
            > a {
              height: $menu-container-height-xl;
              line-height: $menu-container-height-xl;
            }
          }
        }
      }
    }
  }

  .navik-header-container {
    width: 100%;
    max-width: $menu-container-width-xl;
    height: auto;
    min-height: $menu-container-height-xl;
    padding-right: $menu-container-px-xl;
    padding-left: $menu-container-px-xl;
    margin-right: auto;
    margin-left: auto;
  }

  .navik-menu {

    > ul {
      > li {

        &:last-child {
          > a {
            padding-right: $menu-list-pr-xl;
          }
        }

        > a {
          height: $menu-container-height-xl;
          line-height: $menu-container-height-xl;
          color: $menu-color-base !important;
          box-shadow: 0 $menu-box-shadow-width 0 0 transparent !important;

          &::after {
            position: absolute;
            top: $menu-bottom-caret-top;
            left: 50%;
            content: '';
            width: 0;
            height: 0;
            opacity: 0;
            border-top: $menu-bottom-caret-border-width solid $menu-box-shadow-color;
            border-right: $menu-bottom-caret-border-width solid transparent;
            border-left: $menu-bottom-caret-border-width solid transparent;
            transform: translateX(-50%);
          }
        }

        &.current-menu,
        &:hover {
          > a {
            color: $menu-active-color !important;
            box-shadow: 0 $menu-box-shadow-width 0 0 $menu-box-shadow-color !important;
          }
        }

        &.dropdown_menu {

          > a {

            > span {
              &::before {
                border-color: $menu-caret-color !important;
                transition: all $transition-duration-base ease-in-out;
              }
            }

            &::after {
              transition: all $transition-duration-base ease-in-out;
            }
          }

          &.current-menu,
          &:hover {
            > a {

              > span {
                &::before {
                  border-color: $menu-active-color !important;
                }
              }
            }
          }

          &:hover {
            > a {
              &::after {
                opacity: 1;
              }
            }
          }
        }
      }
    }

    ul {

      li {

        > a {
          padding-right: $menu-list-pr-xl;
          padding-left: $menu-list-pl-xl;
        }

        &.dropdown_menu {
          > a {
            > span {
              &::before {
                top: $menu-arrow-top;
                border-color: $submenu-color;
              }
            }
          }
        }
      }

      ul {
        li {

          > a {
            padding: $submenu-list-py $submenu-list-px !important;
            color: $submenu-color !important;

            &::before {
              display: none;
            }
          }

          &:hover {
            > a {
              padding-left: $submenu-list-px !important;
            }
          }

          &.dropdown_menu {
            > a {
              > span {
                &::before {
                  top: $submenu-caret-top;
                  right: $submenu-caret-right;
                }
              }
            }
          }
        }
      }
    }

    li {
      > ul {
        top:$submenu-parent-top;
        width: $submenu-width;
        padding: 0;
        background-color: $submenu-background-color;
        box-shadow: $submenu-box-shadow;

        ul {
          top: 0;
        }
      }
    }
  }

}
