/* =============================================
8.0 Buttons
================================================ */
.btn {
  padding-right: $btn-custom-px;
  padding-left: $btn-custom-px;
  font-family: $btn-font-family;
  font-size: $btn-font-size;
  color: $btn-text-color;
  line-height: $btn-line-height;
  text-transform: uppercase;

  &:hover {
    background-color: $btn-active-color;
    border-color: $btn-active-color;
  }
}

.btn-sm {
  padding-right: $btn-custom-px-sm;
  padding-left: $btn-custom-px-sm;
  font-size: $btn-font-size-sm;
  line-height: $btn-line-height-sm;
}

.btn-lg {
  padding-right: $btn-custom-px-lg;
  padding-left: $btn-custom-px-lg;
  font-size: $btn-font-size-lg;
  line-height: $btn-line-height-lg;
}

.btn-primary {
  background-color: $btn-primary-color;
  border-color: $btn-primary-color;

  &:active,
  &:focus,
  &.focus {
    background-color: darken($btn-primary-color, 10%) !important;
    border-color: darken($btn-primary-color, 10%) !important;
  }
}

.btn-light {
  color: $btn-light-text-color;

  &:hover {
    color: $btn-light-active-text-color;
    background-color: $btn-active-primary-color;
    border-color: $btn-active-primary-color;
  }
}

.btn-dark {
  &:hover {
    background-color: $btn-active-primary-color;
    border-color: $btn-active-primary-color;
  }
}
