/* =============================================
17.0 Blog
================================================ */
.blog-title {
  font-size: $blog-title-font-size;
}

.metadata-wrapper {
  font-family: $blog-meta-font-family;
  text-transform: uppercase;
}

.meta-date {
  width: $meta-date-width;
  height: $meta-date-width;
  font-size: $meta-date-font-size;
  font-weight: $meta-date-font-weight;
  line-height: $meta-date-line-height;
  color: $meta-date-color;
  background-color: $meta-date-bg-color;

  span {
    display: block;
    margin-bottom: $meta-date-month-mb;
    font-size: $meta-date-month-font-size;
    font-weight: $meta-date-month-font-weight;
  }
}

.blog-meta-list {
  font-size: $metadata-font-size;

  li {
    display: inline;
    margin-right: $metadata-mr;

    &:last-child {
      margin-right: 0;
    }

    a {
      margin-left: $metedata-value-ml;
    }
  }
}

.blog-entry {
  padding-bottom: $blog-entry-pb;
  margin-bottom: $blog-entry-mb;
  border-bottom: $blog-entry-border-width solid $blog-entry-border-color;

  &:last-child {
    margin-bottom: 0;
  }
}

.blog-entry-thumbnail {
  margin-bottom: $blog-entry-thumbnail-mb;

  .hover-item {
    > img {
      width: 100%;
    }
  }

  .hover-overlay {
    &::before,
    &::after {
      display: none;
    }
  }
}

/* =============================================
17.1 Blog single
================================================ */
.blog-section-title {
  margin-bottom: $blog-section-title-mb;
  font-size: $blog-section-title-font-size;
  text-transform: uppercase;
}

.author-info {
  padding-top: $author-info-padding;
  border-top: $author-info-border-width solid $author-info-border-color;
}

.author-title {
  margin-bottom: $author-title-mb;
  font-size: $author-title-font-size;
  text-transform: uppercase;
}

.related-blog-heading {
  margin-bottom: 0;
  font-size: $related-blog-heading-font-size;
  font-weight: $related-blog-heading-font-weight;

  a {
    color: $related-blog-heading-color;
    transition: all $transition-duration-base ease-in-out;

    &:hover {
      color: $related-blog-heading-color-hover;
    }
  }
}

.comment-list {

  .media {
    margin-bottom: $comment-list-mb;

    &:last-child {
      margin-bottom: 0;
    }

    &.active {
      .media-body {
        border-color: $comment-active-border-color;
      }
    }
  }

  .media-body {
    border-left: $comment-border-width solid $comment-border-color;
  }
}

.comment-avatar {
  width: $comment-avatar-width-sm;
  height: auto;
}

.comment-name {
  font-family: $comment-name-font-family;
  font-weight: $comment-name-font-weight;
  color: $comment-name-color;
  text-transform: uppercase;
}

.comment-date {
  color: $comment-date-color;
}

.comment-reply {
  a {
    font-family: $comment-reply-font-family;
    font-weight: $comment-reply-font-weight;
    color: $comment-reply-color;
    text-transform: uppercase;
    transition: all $transition-duration-base ease-in-out;

    &:hover {
      color: $comment-reply-color-hover;
    }
  }
}

/* ========== Media query breakpoint ========== */
@include media-breakpoint-up(md) {

  .comment-avatar {
    width: $comment-avatar-width-md;
  }

  .meta-date {
    margin-right: $meta-date-mr;
  }

  .author-img-wrapper {
    padding-right: $author-info-padding + 5;
  }

  .author-info {
    padding-top: 0;
    padding-left: $author-info-padding + 5;
    border-top: none;
    border-left: $author-info-border-width solid $author-info-border-color;
  }

}
