/* =============================================
12.0 Fullscreen gallery
================================================ */
#componentWrapper {
  background-color: $gallery-bg-color;

  .playlist {
    > ul {
      margin: 0;

      > li {
        > a {
          > img {
            width: 100%;
            height: auto;
          }
        }
      }
    }
  }

  .playlistItem,
  .playlistItem2,
  .playlistItem3,
  .playlistItem4,
  .playlistItem5,
  .playlistItem6 {
    width: $gallery-thumbnail-width;
    height: $gallery-thumbnail-height;
    border: $gallery-thumbnail-border-width solid $gallery-thumbnail-border-color;
  }

  .gallery_fullscreen {
    display: none;
  }

  .music_toggle,
  .slideshow_controls,
  .data_controls {
    top: $gallery-data-controls-top-sm;
  }

  .info_holder {
    top: $gallery-info-holder-top-sm;
    font-family: $font-family-base;
    border-radius: $gallery-info-holder-border-radius;
  }

  .audioPlayer {
    top: $gallery-info-holder-top-sm;

    .playerHolder {
      border-radius: $gallery-info-holder-border-radius;
    }

    .player_mediaName {
      font-family: $font-family-base;
    }
  }

  .menu_item {
    font-family: $font-family-secondary;
    text-transform: uppercase;
  }

  .caption_bg_white {
    @extend %gallery-caption-settings;
    color: $gallery-caption-color;
    background-color: $white;

    &.top {
      @extend %gallery-caption-first-top;
    }

    &.bottom {
      @extend %gallery-caption-second-top;
    }
  }

  .caption_bg_dark {
    @extend %gallery-caption-settings;
    color: $white;
    background-color: $gallery-caption-dark-bg;

    &.top {
      @extend %gallery-caption-first-top;
    }

    &.bottom {
      @extend %gallery-caption-second-top;
    }
  }

  .caption_bg_green {
    @extend %gallery-caption-settings;
    color: $white;
    background-color: $gallery-caption-bg-green-color;

    &.top {
      @extend %gallery-caption-first-top;
    }

    &.bottom {
      @extend %gallery-caption-second-top;
    }
  }
}

%gallery-caption-settings {
  padding: $gallery-caption-padding-sm;
  margin-left: $gallery-caption-ml;
  font-family: $font-family-secondary;
  font-size: $gallery-caption-font-size;
  font-weight: $gallery-caption-font-weight;
  text-transform: uppercase;
}

%gallery-caption-first-top {
  margin-top: $gallery-caption-first-top-sm;
}

%gallery-caption-second-top {
  margin-top: $gallery-caption-second-top-sm;
}

.share_controls_wrap {
  top: $share-controls-wrap-top-sm;
  left: $share-controls-wrap-left;
}

.share_controls {
  top: $share-controls-offset;
  left: 0;

  .facebook_toggle,
  .twitter_toggle,
  .tumblr_toggle {
    float: none;
    margin-right: 0;
    margin-bottom: $share-controls-item-offset;
  }
}

/* ========== Media query breakpoint ========== */
@include media-breakpoint-up(md) {

  #componentWrapper {
    .caption_bg_white,
    .caption_bg_dark,
    .caption_bg_green {

      &.top {
        margin-top: $gallery-caption-first-top-md;
      }

      &.bottom {
        margin-top: $gallery-caption-second-top-md;
      }
    }
  }

  .share_controls {
    top: 0;
    left: $share-controls-offset;
  
    .facebook_toggle,
    .twitter_toggle,
    .tumblr_toggle {
      float: left;
      margin-right: $share-controls-item-offset;
      margin-bottom: 0;
    }
  }

}

@include media-breakpoint-up(lg) {

  #componentWrapper {
    .caption_bg_white,
    .caption_bg_dark,
    .caption_bg_green {
      padding: $gallery-caption-padding-lg;

      &.top {
        margin-top: $gallery-caption-first-top-lg;
      }

      &.bottom {
        margin-top: $gallery-caption-second-top-lg;
      }
    }
  }

}

@include media-breakpoint-up(xl) {

  #componentWrapper {

    .music_toggle,
    .slideshow_controls,
    .data_controls {
      top: $gallery-data-controls-top-xl;
    }

    .info_holder,
    .audioPlayer {
      top: $gallery-info-holder-top-xl;
    }

    .caption_bg_white,
    .caption_bg_dark,
    .caption_bg_green {

      &.top {
        margin-top: $gallery-caption-first-top-xl;
      }

      &.bottom {
        margin-top: $gallery-caption-second-top-xl;
      }
    }
  }

  .share_controls_wrap {
    top: $share-controls-wrap-top-xl;
  }

}
