/* =============================================
21.0 Contact
================================================ */
.contact-details-title {
  margin-bottom: $contact-details-title-mb;
  font-size: $contact-details-title-size;
  text-transform: uppercase;
}

.contact-info-list {
  padding-left: $contact-info-list-pl;
  margin-bottom: $contact-info-list-mb;
  border-left: $contact-info-list-border-width solid $contact-info-list-border-color;

  ul {
    li {
      margin-bottom: $contact-info-list-li-mb;
      font-family: $contact-info-list-font-family;
      font-weight: $contact-info-list-font-weight;
      color: $contact-info-list-color;
      text-transform: uppercase;

      span {
        color: $contact-info-list-value-color;
        text-transform: none;
      }
    }
  }
}
