/* =============================================
24.0 Back to top
================================================ */
.back-to-top {
  position: fixed;
  z-index: 1000;
  visibility: hidden;
  right: $back-to-top-offset;
  bottom: $back-to-top-offset;
  width: $back-to-top-width;
  height: $back-to-top-width;
  background-color: $back-to-top-bg-color;
  border-radius: 50%;
  opacity: 0;
  box-shadow: $back-to-top-shadow;
  transition: all $transition-duration-base ease-in-out;

  &.backToTop-on {
    visibility: visible;
    opacity: 1;
  }

  &:hover {
    background-color: $white;
  }
}
