/* =============================================
22.0 Widgets
================================================ */
.widget {
  margin-bottom: $widget-mb;

  &:last-child {
    margin-bottom: 0;
  }
}

.widget-title {
  margin-bottom: $widget-title-mb;
  font-size: $widget-title-font-size;
  text-transform: uppercase;
}

.widget-list {
  li {
    transition: all ( $transition-duration-base + .1 ) ease-in-out;

    a {
      color: $widget-list-color;
      transition: all ( $transition-duration-base + .1 ) ease-in-out;
    }

    &:hover {
      padding-left: $widget-list-hover-pl;

      a {
        color: $widget-list-color-hover;
      }
    }
  }
}

.widget-list-title {
  font-size: $widget-list-font-size;
  font-weight: $widget-list-font-weight;
}

.widget-recent-news {
  .media-body {
    a {
      font-family: $widget-recent-news-font-family;
      font-size: $widget-recent-news-font-size;
      text-transform: uppercase;
    }
  }
}

.widget-gallery {
  margin-right: -$widget-gallery-gutter;
  margin-left: -$widget-gallery-gutter;
}

.widget-gallery-col {
  padding-right: $widget-gallery-gutter !important;
  padding-left: $widget-gallery-gutter !important;
  margin-bottom: $widget-gallery-gutter * 2;

  img {
    width: 100%;
  }
}

.widget-tags-wrapper {
  a {
    float: left;
    padding: $widget-tags-py $widget-tags-px;
    margin: 0 $widget-tags-offset $widget-tags-offset 0;
    color: $widget-tags-color;
    background-color: $widget-tags-bg-color;
    border-radius: $widget-tags-border-radius;
    transition: all ( $transition-duration-base + .2 ) ease-in-out;

    &:hover {
      background-color: $widget-tags-bg-color-hover;
    }
  }
}
