// Extended theme colors
$primary:                                       #36bfc4 !default;
$secondary:                                     #16a085 !default;
$success:                                       #2ecc71 !default;
$info:                                          #5dade2 !default;
$warning:                                       #f1c40f !default;
$danger:                                        #e74c3c !default;
$light:                                         #ecf0f1 !default;
$dark:                                          #2e2e2e !default;

$theme-colors: () !default;
$theme-colors: map-merge(
  (
    "primary":                                  $primary,
    "secondary":                                $secondary,
    "success":                                  $success,
    "info":                                     $info,
    "warning":                                  $warning,
    "danger":                                   $danger,
    "light":                                    $light,
    "dark":                                     $dark
  ),
  $theme-colors
);

