/* =============================================
23.0 Footer
================================================ */
.footer {
  background-color: $footer-bg-color;
}

.footer-widget-title {
  font-size: $footer-widget-title-font-size;
}

.footer-address {
  li {
    margin-bottom: $footer-list-mb;

    &::before {
      top: $footer-list-plus-before-top
    }

    &::after {
      top: $footer-list-plus-after-top
    }
  }
}

.footer-social {

  li {
    float: left;
    margin: 0 $footer-social-offset $footer-social-offset 0;

    &:last-child {
      margin-right: 0;
    }

    a {
      display: block;
      width: $footer-social-width;
      height: $footer-social-width;
      background-color: $footer-social-bg-color;
      border-radius: 50%;
      transition: all $transition-duration-base ease-in-out;

      img {
        vertical-align: top;
      }

      &:hover {
        background-color: $social-hover-color-1;
      }
    }

    &:nth-child(2) {
      a {
        &:hover {
          background-color: $social-hover-color-2;
        }
      }
    }

    &:nth-child(3) {
      a {
        &:hover {
          background-color: $social-hover-color-3;
        }
      }
    }

    &:nth-child(4) {
      a {
        &:hover {
          background-color: $social-hover-color-3;
        }
      }
    }

    &:nth-child(5) {
      a {
        &:hover {
          background-color: $social-hover-color-3;
        }
      }
    }

    &:nth-child(6) {
      a {
        &:hover {
          background-color: $social-hover-color-3;
        }
      }
    }

    &:nth-child(7) {
      a {
        &:hover {
          background-color: $social-hover-color-3;
        }
      }
    }

    &:nth-child(8) {
      a {
        &:hover {
          background-color: $social-hover-color-3;
        }
      }
    }
  }
}

.footer-bottom {
  padding: $footer-bottom-py 0;
  font-size: $footer-bottom-font-size;
  color: $footer-bottom-text-color;
  background-color: $footer-bottom-bg-color;

  a {
    text-decoration: none;
    transition: all $transition-duration-base ease-in-out;
  }
}

.footer-link {
  margin: 0;

  li {
    display: inline;

    a {
      color: $footer-bottom-text-color;

      &:hover {
        color: $footer-link-hover-color;
      }
    }
  }
}
