/* =============================================
14.0 Homepage
================================================ */

/* =============================================
14.1 Homepage style 2
================================================ */
.services-section {
  background-image: $services-section-bg-image;
  background-repeat: repeat;
}

.service-row {
  position: relative;
  z-index: 30;
  margin-top: $service-hex-offset-top-sm;
}

.service-item {
  position: relative;

  .service-item-info {
    position: absolute;
    width: 100%;
    top: 50%;
    text-align: center;
    opacity: $service-hex-opacity;
    transform: translateY(-50%);
    transition: all $transition-duration-base ease-in-out;

    img {
      margin-bottom: $service-hex-img-mb;
    }

    h4 {
      margin-bottom: 0;
      font-size: $service-hex-font-size;
      font-weight: $service-hex-font-weight;
    }
  }

  &:hover {
    .service-item-info {
      opacity: 1;
    }
  }

  svg {
    fill: $service-hex-fill;
    transition: fill $transition-duration-base ease-out;
  }

  &:hover {
    svg {
      fill: $service-hex-active-color;
    }			
  }
}

.info-main-title {
  margin-bottom: $info-main-title-mb;
  font-size: $info-main-title-font-size-sm;
  font-weight: $info-main-title-font-weight;
}

.info-subtitle {
  font-size: $info-subtitle-font-size;
  font-weight: $info-subtitle-font-weight;
}

/* =============================================
14.2 Homepage style 3
================================================ */
.info-btn-link {
  position: relative;
  padding-left: $info-btn-link-pl-sm;
  font-family: $info-btn-link-font-family;
  font-size: $info-btn-link-font-size-sm;
  font-weight: $info-btn-link-font-weight;
  color: $info-btn-link-color;
  text-decoration: none;
  transition: all $transition-duration-base ease-in-out;

  &:hover {
    color: $active-color;

    &::before,
    &::after {
      background-color: $active-color;
    }
  }

  &::before {
    @extend %info-btn-link-plus;
    top: $info-btn-link-plus-offset-sm;
    left: 0;
    width: $info-btn-link-plus-width-sm;
    height: $info-btn-link-plus-thickness;
  }

  &::after {
    @extend %info-btn-link-plus;
    top: $info-btn-link-plus-offset-sm / 2;
    left: $info-btn-link-plus-offset-sm / 2;
    width: $info-btn-link-plus-thickness;
    height: $info-btn-link-plus-width-sm;
  }
}

%info-btn-link-plus {
  position: absolute;
  content: '';
  background-color: $info-btn-link-color;
  transition: all $transition-duration-base ease-in-out;
}

/* ========== Media query breakpoint ========== */
@include media-breakpoint-up(md) {

  .service-row {
    margin-top: $service-hex-offset-top-md;
  }

  .info-main-title {
    font-size: $info-main-title-font-size-md;
  }

  .info-btn-link {
    padding-left: $info-btn-link-pl-md;
    font-size: $info-btn-link-font-size-md;

    &::before {
    top: $info-btn-link-plus-offset-md;
    width: $info-btn-link-plus-width-md;
  }

    &::after {
      top: $info-btn-link-plus-offset-md - 8;
      left: $info-btn-link-plus-offset-md - 10;
      height: $info-btn-link-plus-width-md;
    }
  }

}

@include media-breakpoint-up(lg) {

  .service-item {
    transition: all $transition-duration-base ease-in-out;

    &:hover {
      transform: translateY(-30px);
    }
  }

}

@include media-breakpoint-up(xl) {

  .service-row {
    margin-top: $service-hex-offset-top-xl;
  }

}
