/* =============================================
18.0 Share this
================================================ */
.share-this {
  padding-top: $share-this-pt;
  overflow: hidden;

  a {
    float: left;
    margin-right: $share-this-link-mr;
    width: $share-this-link-width;
    height: $share-this-link-height;
    color: $share-this-link-color;
    border-radius: $share-this-link-border-radius;
    transition: all $transition-duration-base ease-in-out;

    &.share-this-facebook {
      background-color: $share-this-facebook-color;

      &:hover {
        background-color: $share-this-facebook-color-hover;
      }
    }

    &.share-this-twitter {
      background-color: $share-this-twitter-color;

      &:hover {
        background-color: $share-this-twitter-color-hover;
      }
    }

    &.share-this-pinterest {
      background-color: $share-this-pinterest-color;

      &:hover {
        background-color: $share-this-pinterest-color-hover;
      }
    }
  }
}

.share-this-title {
  margin-bottom: $share-this-title-mb;
  font-size: $share-this-title-font-size;
  text-transform: uppercase;
}
