/* =============================================
3.0 Components
================================================ */
.bg-image {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.list-plus {
  li {
    position: relative;
    padding-left: $list-plus-pl;

    &::before {
      @extend %list-plus;
      top: $list-plus-before-top;
      left: 0;
      width: $list-plus-width;
      height: $list-plus-thickness;
    }

    &::after {
      @extend %list-plus;
      top: $list-plus-after-top;
      left: $list-plus-after-left;
      width: $list-plus-thickness;
      height: $list-plus-width;
    }
  }
}

%list-plus {
  position: absolute;
  content: '';
  background-color: $list-plus-color;
}

.divider {
  width: 100%;
  height: $divider-height;
  background-color: $divider-color;
}
