/* =============================================
6.0 Hover
================================================ */
.hover-item {
  position: relative;

  .hover-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $hover-overlay-color;
    opacity: 0;
    transition: all ( $transition-duration-base + .1 ) ease-in-out;

    &::before {
      @extend %hover-overlay-plus;
      width: $hover-plus-width;
      height: $hover-plus-thickness;
    }

    &::after {
      @extend %hover-overlay-plus;
      width: $hover-plus-thickness;
      height: $hover-plus-width;
    }
  }

  &:hover {
    .hover-overlay {
      opacity: $hover-overlay-opacity;
    }
  }
}

%hover-overlay-plus {
  position: absolute;
  content: '';
  top: 50%;
  left: 50%;
  background-color: $white;
  transform: translate(-50%, -50%);
}
