/* =============================================
19.0 Clients
================================================ */
.client-col {
  padding: $client-col-padding !important;
  text-align: center;

  img {
    position: relative;
    z-index: 1;
  }

  &::before {
    @extend %client-col-divider;
    top: $client-border-offset;
    bottom: $client-border-offset;
    left: 0;
    right: 0;
    border-right: $client-border-width solid $client-border-color;
  }

  &::after {
    @extend %client-col-divider;
    top: 0;
    bottom: 0;
    left: $client-border-offset;
    right: $client-border-offset;
    border-bottom: $client-border-width solid $client-border-color;
  }

  &:nth-child(even) {
    &::before {
      border-right: none;
    }
  }
}

%client-col-divider {
  position: absolute;
  content: '';
  z-index: 0;
}

.client-col-bottom-line {
  &::before {
    display: block;
    content:'';
    height: $client-col-bottom-line-height;
    margin-top: -$client-col-bottom-line-height;
    background-color: $client-col-bottom-line-color;
  }
}

/* ========== Media query breakpoint ========== */
@include media-breakpoint-up(md) {

  .client-col {

    &:nth-child(even) {
      &::before {
        border-right: $client-border-width solid $client-border-color;
      }
    }

    &:nth-child(4n) {
      &::before {
        border-right: none;
      }
    }
  }

}

@include media-breakpoint-up(lg) {

  .client-col {

    &:nth-child(4n) {
      &::before {
        border-right: $client-border-width solid $client-border-color;
      }
    }

    &:nth-child(6n) {
      &::before {
        border-right: none;
      }
    }
  }

}
